<template>
  <div>
    <LayoutBox one="订单详情">
      <div class="buyer_infor">
        <div class="buyer_infor_bottom"><h4>买家信息</h4></div>
        <div class="buyer_infor_box">
          <span>用户昵称：<span>{{ orderListData.userName }}</span></span>
          <span>支付方式：<span>{{ orderListData.payType === 2 ? '微信' : orderListData.payType === 1 ? '支付宝' : '暂未支付' }}</span></span>
          <span>收货地址：<span>{{ orderListData.receiverName }}<span>{{ orderListData.receiverPhone }}</span></span></span>
          <span class="address">{{ orderListData.receiverAddress }}{{ orderListData.receiverDetailAddress }}</span>
        </div>
      </div>

      <div class="order_details">
        <div class="order_details_bottom"><h4>订单详情</h4></div>
        <div class="text_box">
          <span>订单编号：<span>{{ orderListData.orderCode }}</span></span>
          <span>下单时间：<span>{{ orderListData.createTime }}</span></span>
          <span>付款时间：<span>{{ orderListData.payTime }}</span></span>
        </div>
      </div>
      <el-table class="goods_table" :data="orderData">
        <el-table-column label="商品" width="250" align="center">
          <template slot-scope="scope">
            <div class="goods_box">
              <el-image :src="scope.row.prodUrl" fit="cover" style="width: 70px; height: 70px"></el-image>
              <span class="goods_name">{{ scope.row.prodName }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="categoryName" label="商品属性" width="120" align="center"></el-table-column>
        <el-table-column prop="status" label="状态" align="center">
          <template>
            <!-- 订单状态 0-已下单 1-待揽件 3-待评价 9-已关闭 10-已完成-->
            <span v-if="orderListData.orderStatus === 0">已下单</span>
            <span v-else-if="orderListData.orderStatus === 1">待揽件</span>
            <span v-else-if="orderListData.orderStatus === 2">配送中</span>
            <span v-else-if="orderListData.orderStatus === 3">待评价</span>
            <span v-else-if="orderListData.orderStatus === 10">已完成</span>
            <span v-else>已关闭</span>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="单价" align="center"></el-table-column>
        <el-table-column prop="count" label="数量" align="center"></el-table-column>
        <el-table-column prop="discountPrice" label="优惠" align="center"></el-table-column>
        <el-table-column prop="totalPrice" label="商品总价" align="center"></el-table-column>
      </el-table>

      <div class="pay_money">
        <div>
          <!-- 订单状态 1-已下单 3-待评价 9-已关闭 10-已完成-->
          <el-button v-if="orderListData.orderStatus === 1" style="width: 100px" size="small" round @click="closeOrder">取消订单</el-button>
          <el-button v-if="orderListData.orderStatus === 1" class="inquire" style="width: 100px" type="success" size="small" round @click="onConfirm(scope)">确认送达</el-button>
          <el-button style="width: 100px" type="success" size="small" round @click="onPrintCode">打印</el-button>
        </div>
        <div>
          <span>实付款：</span>
          <span class="money">{{ orderListData.payAmount }}</span>
          <span>元</span>
        </div>
      </div>
    </LayoutBox>
    <lodopCmp ref="lodop" />
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import lodopCmp from '@/components/lodop_cmp/index.vue'

export default {
  name: 'OrderContent',
  components: {
    LayoutBox,
    lodopCmp
  },

  data() {
    return {
      pageNo: 1, // 订单详情下的的商品分页当前页数默认1第一页
      orderHeaderId: '', // 订单id
      orderData: [], // 订单明细数据
      orderListData: '' // 订单列表数据
    }
  },

  created () {
    this.orderHeaderId = this.$route.query.id
  },

  mounted () {
    this._findOrderList()
    this._getOrderData()
  },

  methods: {
    // 关闭订单
    closeOrder () {
      this.$confirm('此操作将导致订单失效, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._orderClose()
      })
    },
    // 点击确认
    onConfirm () {
      this.$confirm('订单确定送达了吗? 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.apiOrderFinish()
      })
    },
    // 点击打印
    onPrintCode () {
      const data = {
        ...this.orderListData,
        logistcsNumber: this.orderListData.orderCode,
      }
      this.$refs.lodop.printCode(data)
    },

    // 接口api
    // 获取订单明细
    _getOrderData () {
      const DATA = {
        pageNo: this.pageNo,
        orderHeaderId: this.orderHeaderId
      }
      this.$http.post('/woho/order/item/query', DATA)
        .then((res) => {
          this.orderData = res.data.data.list
        })
    },
    // 根据id查询订单
    _findOrderList () {
      this.$http.post('/woho/order/head/findById/' + this.orderHeaderId)
        .then((res) => {
          this.orderListData = res.data.data
          console.log(res.data.data)
        })
    },
    // 订单关闭
    _orderClose () {
      this.$http.post('/woho/order/head/orderClose/' + this.orderHeaderId)
        .then((res) => {
          console.log(res.data.data)
          if (res.data.success) {
            this.$router.push('/superMarket/orderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 订单完成
    apiOrderFinish () {
      this.$http.post(`/woho/order/head/orderFinish/${this.orderListData.id}`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$router.push('/superMarket/orderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .order_status{
    width: 90%;
    background-color: #e6fffc;
    border-radius: 5px;
    border: solid 1px $themColor;
    margin: 56px auto auto auto;
    padding-left: 25px;
    .express{
      color: $themColor;
      display: inline-block;
      margin: 15px 0px;
    }
  }
  .buyer_infor{
    width: 90%;
    margin: 25px auto auto auto;
    .buyer_infor_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .buyer_infor_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      .address{
        margin-left: 70px;
      }
    }
  }
  .order_details{
    width: 90%;
    margin: 66px auto auto auto;
    .order_details_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .text_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      width: 70%;
    }
  }
  .goods_table{
      width: 90%;
      margin: 15px auto;
    .goods_box{
      display: flex;
      align-items: center;
      .goods_name{
        width: 150px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        text-align: left;
        margin: 5px;
      }
    }
  }
  .pay_money{
    font-size: 14px;
    font-weight: bold;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    .money{
      color: #f9930f;
    }
  }
</style>
